<template>
  <div id="details">
    <mt-header fixed title="新闻详情" class="header">
      <mt-button slot="left" @click="back">返回</mt-button>
      <mt-button slot="right" @click="collect(item)">收藏</mt-button>
    </mt-header>
    <div class="box">
      <div class="title">{{item.title}}</div>
      <div class="content" v-html="item.content"></div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      item: {} 
    };
  },
  mounted() {
    this.item = this.$route.params.item;

    // 获取屏幕宽度，给header设置与屏幕宽度相同的宽度
    window.screenWidth = document.body.clientWidth;
    const header = document.getElementsByClassName("header")[0];
    header.style.width = window.screenWidth + "px";
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    collect(item) {
      const collectList = [];
      const itemObj = [
        {
          title: item.title,
          pic: item.pic,
          category: item.category,
          src: item.src,
          time: item.time
        }
      ];
      const collect = window.localStorage.getItem("collect");
      // console.log(Boolean(collect));

      if (collect) {
        var arr = JSON.parse(collect).concat(itemObj);
        var str = JSON.stringify(arr);
      } else {
        var str = JSON.stringify(itemObj);
        // window.localStorage.setItem("collect", str);
      }
      new Promise((res, rej) => {
        setTimeout(() => {
          window.localStorage.setItem("collect", str);
          res();
        }, 1000);
      }).then(() => {
        this.$toast("收藏成功");
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    from.meta.keepAlive = true;
    next();
  },
  beforeRouteLeave(to, from, next) {
    to.meta.keepAlive = true;
    next();
  }
};
</script>
<style scoped>
#details {
  width: 100%;
  overflow: hidden;
  padding-bottom: 5px;
}

/* .header {
  width: this.width;
} */

.box {
  margin-top: 50px;
  padding: 0 8px 45px;
}

.title {
  text-align: center;
  font-size: 17px;
  font-weight: bold;
}

.content {
  text-indent: 2em;
  text-align: justify;
}

.content img {
  max-width: 100%;
}
</style>